import React from 'react'
import Link from 'gatsby-link'

import HomeHeader from '../assets/HomeHeader.js'
import AvatarDance from '../assets/AvatarDance.js'
import IconArchive from '../assets/IconArchive.js'
import IconWork from '../assets/IconWork.js'
import IconContact from '../assets/IconContact.js'

const IndexPage = ({ data }) => (
  <div>
    <nav className="nav mobile-only">
      <div className="nav__inner">
        <ul>
          <li>
            <Link to="/archive">
              <IconArchive />
              <span>Archive</span>
            </Link>
          </li>
          <li>
            <Link to="/work">
              <IconWork />
              <span>Work</span>
            </Link>
          </li>
          <li>
            <Link to="/contact">
              <IconContact />
              <span>Contact</span>
            </Link>
          </li>
        </ul>
      </div>
    </nav>

    <main className="home">
      <header className="home__header">
        <div className="header__container">
          <div className="left__column">
            <h1>
              <span>Welcome to</span> modayil.me
            </h1>
            <p>
              <span>Benjamin Modayil</span> is a Front-End Developer from Philadelphia,
              Pennsylvannia.
            </p>
            <Link to="/work" className="btn">
              Learn more
            </Link>
          </div>

          <div className="right__column">
            <HomeHeader />
            <nav className="nav nav__home">
              <div className="nav__inner desktop-only">
                <ul>
                  <li>
                    <Link to="/archive">
                      <IconArchive />
                      <span>Archive</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/work">
                      <IconWork />
                      <span>Work</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/contact">
                      <IconContact />
                      <span>Contact</span>
                    </Link>
                  </li>
                </ul>
              </div>
            </nav>
          </div>
        </div>
      </header>
      <div className="home__content wrapper">
        <div className="row">
          <div className="home__module component__list home__updates">
            <h2>
              <span>updates</span> from my life
            </h2>
            <ul>
              <li>
                <Link to="">
                  <small>date</small>
                  <h3>title</h3>
                  <p>tldr</p>
                </Link>
              </li>
            </ul>
          </div>

          <div className="home__module component__list home__featured">
            <h2>
              my <span>featured</span> work
            </h2>
            <ul>
              <li>
                <Link to="/work-preview.html">
                  <h3>Lighthouse Hill</h3>
                  <p>My Web Design Capstone Project</p>
                </Link>
              </li>
              <li>
                <Link to="/work-preview.html">
                  <h3>GroundRulesCo</h3>
                  <p>Group Project with other Viget Interns</p>
                </Link>
              </li>
              <li>
                <Link to="/work-preview.html">
                  <h3>Polygon Animation</h3>
                  <p>description goes here</p>
                </Link>
              </li>
            </ul>
          </div>
        </div>
        <div className="row">
          <div className="home__module home__about">
            <section>
              <h2>
                <span>about</span> .me
              </h2>
              include about
            </section>
          </div>
          <div className="svg__animation-dance home__module">
            <AvatarDance />
          </div>
        </div>
      </div>
    </main>
  </div>
)

// {data.allMarkdownRemark.edges.map(post => (
//   <Link key={post.node.id} to={post.node.frontmatter.path}>
//     {post.node.frontmatter.title}
//   </a>
// ))}
export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(limit: 10) {
      edges {
        node {
          id
          frontmatter {
            title
            path
          }
        }
      }
    }
  }
`
export default IndexPage



